'use client';

import React, { useState, useCallback, useMemo } from 'react';
import {
  Card,
  Typography,
  Tag,
  Avatar,
  Space,
  Button,
  Dropdown,
  Tooltip,
  Badge,
  Input,
  Select,
  Row,
  Col,
  Modal,
  Form,
  message,
} from 'antd';
import {
  MoreOutlined,
  EditOutlined,
  DeleteOutlined,
  UserOutlined,
  CalendarOutlined,
  ClockCircleOutlined,
  PlusOutlined,
  FilterOutlined,
  SortAscendingOutlined,
} from '@ant-design/icons';
import type { MenuProps } from 'antd';
import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';
import 'dayjs/locale/zh-cn';

import { useRouter } from 'next/navigation';
import type { Ticket } from '@/app/lib/api-config';
import { useTicketListStore } from '@/lib/stores/ticket-store';

dayjs.extend(relativeTime);
dayjs.locale('zh-cn');

const { Title, Text, Paragraph } = Typography;
const { Search } = Input;
const { Option } = Select;

interface TicketKanbanProps {
  onTicketSelect?: (ticket: Ticket) => void;
}

// Áä∂ÊÄÅÈÖçÁΩÆ
const KANBAN_STATUS_CONFIG = [
  { key: 'new', title: 'Êñ∞Âª∫', color: '#1890ff' },
  { key: 'open', title: 'ÂæÖÂ§ÑÁêÜ', color: '#1890ff' },
  { key: 'in_progress', title: 'Â§ÑÁêÜ‰∏≠', color: '#fa8c16' },
  { key: 'pending', title: 'Á≠âÂæÖ‰∏≠', color: '#faad14' },
  { key: 'resolved', title: 'Â∑≤Ëß£ÂÜ≥', color: '#52c41a' },
  { key: 'closed', title: 'Â∑≤ÂÖ≥Èó≠', color: '#d9d9d9' },
];

// ‰ºòÂÖàÁ∫ßÈÖçÁΩÆ
const PRIORITY_CONFIG = {
  low: { color: 'green', text: '‰Ωé', icon: '‚Üì' },
  medium: { color: 'orange', text: '‰∏≠', icon: '‚Üí' },
  high: { color: 'red', text: 'È´ò', icon: '‚Üë' },
  urgent: { color: 'purple', text: 'Á¥ßÊÄ•', icon: '‚ö°' },
  critical: { color: 'red', text: '‰∏•Èáç', icon: 'üö®' },
};

const TicketKanban: React.FC<TicketKanbanProps> = ({ onTicketSelect }) => {
  const router = useRouter();
  const { tickets, loading, fetchTickets, updateTicket, deleteTicket } = useTicketListStore();
  
  const [searchValue, setSearchValue] = useState('');
  const [selectedStatus, setSelectedStatus] = useState<string>('');
  const [selectedPriority, setSelectedPriority] = useState<string>('');
  const [sortBy, setSortBy] = useState<string>('created_at');
  const [viewModalVisible, setViewModalVisible] = useState(false);
  const [selectedTicket, setSelectedTicket] = useState<Ticket | null>(null);

  // ËøáÊª§ÂíåÊéíÂ∫èÂ∑•Âçï
  const filteredTickets = useMemo(() => {
    let filtered = [...tickets];

    // ÊêúÁ¥¢ËøáÊª§
    if (searchValue) {
      filtered = filtered.filter(ticket =>
        ticket.title.toLowerCase().includes(searchValue.toLowerCase()) ||
        ticket.description.toLowerCase().includes(searchValue.toLowerCase()) ||
        ticket.ticket_number.toLowerCase().includes(searchValue.toLowerCase())
      );
    }

    // Áä∂ÊÄÅËøáÊª§
    if (selectedStatus) {
      filtered = filtered.filter(ticket => ticket.status === selectedStatus);
    }

    // ‰ºòÂÖàÁ∫ßËøáÊª§
    if (selectedPriority) {
      filtered = filtered.filter(ticket => ticket.priority === selectedPriority);
    }

    // ÊéíÂ∫è
    filtered.sort((a, b) => {
      switch (sortBy) {
        case 'priority':
          const priorityOrder = { critical: 4, urgent: 3, high: 2, medium: 1, low: 0 };
          return (priorityOrder[b.priority as keyof typeof priorityOrder] || 0) - 
                 (priorityOrder[a.priority as keyof typeof priorityOrder] || 0);
        case 'title':
          return a.title.localeCompare(b.title);
        case 'created_at':
        default:
          return new Date(b.created_at).getTime() - new Date(a.created_at).getTime();
      }
    });

    return filtered;
  }, [tickets, searchValue, selectedStatus, selectedPriority, sortBy]);

  // ÊåâÁä∂ÊÄÅÂàÜÁªÑÂ∑•Âçï
  const ticketsByStatus = useMemo(() => {
    const grouped: Record<string, Ticket[]> = {};
    
    KANBAN_STATUS_CONFIG.forEach(status => {
      grouped[status.key] = [];
    });

    filteredTickets.forEach(ticket => {
      if (grouped[ticket.status]) {
        grouped[ticket.status].push(ticket);
      }
    });

    return grouped;
  }, [filteredTickets]);

  // Â∑•ÂçïÊìç‰ΩúËèúÂçï
  const getTicketMenu = (ticket: Ticket): MenuProps['items'] => [
    {
      key: 'view',
      icon: <EditOutlined />,
      label: 'Êü•ÁúãËØ¶ÊÉÖ',
      onClick: () => {
        if (onTicketSelect) {
          onTicketSelect(ticket);
        } else {
          router.push(`/tickets/${ticket.id}`);
        }
      },
    },
    {
      key: 'edit',
      icon: <EditOutlined />,
      label: 'ÁºñËæë',
      onClick: () => router.push(`/tickets/${ticket.id}/edit`),
    },
    {
      type: 'divider',
    },
    {
      key: 'delete',
      icon: <DeleteOutlined />,
      label: 'Âà†Èô§',
      danger: true,
      onClick: () => handleDeleteTicket(ticket),
    },
  ];

  // Âà†Èô§Â∑•Âçï
  const handleDeleteTicket = useCallback(async (ticket: Ticket) => {
    Modal.confirm({
      title: 'Á°ÆËÆ§Âà†Èô§',
      content: `Á°ÆÂÆöË¶ÅÂà†Èô§Â∑•Âçï ${ticket.ticket_number} ÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊí§ÈîÄ„ÄÇ`,
      okText: 'Á°ÆËÆ§Âà†Èô§',
      cancelText: 'ÂèñÊ∂à',
      okType: 'danger',
      onOk: async () => {
        try {
          await deleteTicket(ticket.id);
          message.success('Âà†Èô§ÊàêÂäü');
        } catch (error) {
          message.error('Âà†Èô§Â§±Ë¥•');
        }
      },
    });
  }, [deleteTicket]);

  // ÊãñÊãΩÁä∂ÊÄÅÊõ¥Êñ∞
  const handleStatusChange = useCallback(async (ticket: Ticket, newStatus: string) => {
    try {
      await updateTicket(ticket.id, { status: newStatus });
      message.success('Áä∂ÊÄÅÊõ¥Êñ∞ÊàêÂäü');
    } catch (error) {
      message.error('Áä∂ÊÄÅÊõ¥Êñ∞Â§±Ë¥•');
    }
  }, [updateTicket]);

  // Â∑•ÂçïÂç°ÁâáÁªÑ‰ª∂
  const TicketCard = ({ ticket }: { ticket: Ticket }) => {
    const priorityConfig = PRIORITY_CONFIG[ticket.priority as keyof typeof PRIORITY_CONFIG];
    
    return (
      <Card
        size="small"
        hoverable
        className="mb-3 cursor-move"
        style={{
          borderLeft: `4px solid ${priorityConfig.color}`,
          margin: '0 0 12px 0',
        }}
        actions={[
          <Dropdown menu={{ items: getTicketMenu(ticket) }} trigger={['click']}>
            <Button type="text" icon={<MoreOutlined />} size="small" />
          </Dropdown>
        ]}
      >
        <div className="space-y-2">
          {/* Â∑•ÂçïÊ†áÈ¢ò */}
          <div className="flex items-start justify-between">
            <Text strong className="text-sm flex-1 mr-2">
              {ticket.title}
            </Text>
            <Badge
              count={priorityConfig.icon}
              style={{
                backgroundColor: priorityConfig.color,
                fontSize: '12px',
                lineHeight: '16px',
              }}
            />
          </div>

          {/* Â∑•ÂçïÂè∑ÂíåÁ±ªÂûã */}
          <div className="flex items-center justify-between">
            <Text code className="text-xs">{ticket.ticket_number}</Text>
            <Tag color="blue">{ticket.type}</Tag>
          </div>

          {/* Â∑•ÂçïÊèèËø∞ */}
          <Paragraph
            ellipsis={{ rows: 2, expandable: false }}
            className="text-xs text-gray-500 mb-2"
          >
            {ticket.description}
          </Paragraph>

          {/* Êó∂Èó¥‰ø°ÊÅØ */}
          <div className="flex items-center text-xs text-gray-400">
            <ClockCircleOutlined className="mr-1" />
            {dayjs(ticket.created_at).fromNow()}
          </div>

          {/* ÂàÜÈÖç‰∫∫‰ø°ÊÅØ */}
          {ticket.assignee && (
            <div className="flex items-center mt-2">
              <Avatar size="small" icon={<UserOutlined />} className="mr-2" />
              <Text className="text-xs">{ticket.assignee.name || ticket.assignee.username}</Text>
            </div>
          )}

          {/* Êà™Ê≠¢Êó∂Èó¥ */}
          {ticket.due_date && (
            <div className="flex items-center mt-1">
              <CalendarOutlined className="mr-1 text-xs text-red-500" />
              <Text className="text-xs text-red-500">
                Êà™Ê≠¢: {dayjs(ticket.due_date).format('MM-DD HH:mm')}
              </Text>
            </div>
          )}
        </div>
      </Card>
    );
  };

  return (
    <div className="ticket-kanban p-4">
      {/* Â∑•ÂÖ∑Ê†è */}
      <Card className="mb-4">
        <Row gutter={[16, 16]} align="middle">
          <Col flex="auto">
            <Space size="middle">
              <Search
                placeholder="ÊêúÁ¥¢Â∑•Âçï..."
                value={searchValue}
                onChange={e => setSearchValue(e.target.value)}
                style={{ width: 300 }}
                allowClear
              />
              <Select
                placeholder="Áä∂ÊÄÅ"
                value={selectedStatus}
                onChange={setSelectedStatus}
                allowClear
                style={{ width: 120 }}
              >
                {KANBAN_STATUS_CONFIG.map(status => (
                  <Option key={status.key} value={status.key}>
                    {status.title}
                  </Option>
                ))}
              </Select>
              <Select
                placeholder="‰ºòÂÖàÁ∫ß"
                value={selectedPriority}
                onChange={setSelectedPriority}
                allowClear
                style={{ width: 120 }}
              >
                {Object.entries(PRIORITY_CONFIG).map(([key, config]) => (
                  <Option key={key} value={key}>
                    <Tag color={config.color}>{config.text}</Tag>
                  </Option>
                ))}
              </Select>
              <Select
                value={sortBy}
                onChange={setSortBy}
                style={{ width: 140 }}
              >
                <Option value="created_at">ÂàõÂª∫Êó∂Èó¥</Option>
                <Option value="priority">‰ºòÂÖàÁ∫ß</Option>
                <Option value="title">Ê†áÈ¢ò</Option>
              </Select>
            </Space>
          </Col>
          <Col>
            <Space>
              <Button
                type="primary"
                icon={<PlusOutlined />}
                onClick={() => router.push('/tickets/create')}
              >
                Êñ∞Âª∫Â∑•Âçï
              </Button>
            </Space>
          </Col>
        </Row>
      </Card>

      {/* ÁúãÊùøÂàó */}
      <Row gutter={[16, 0]}>
        {KANBAN_STATUS_CONFIG.map(status => (
          <Col span={4} key={status.key}>
            <Card
              title={
                <div className="flex items-center justify-between">
                  <div className="flex items-center">
                    <div
                      className="w-2 h-2 rounded-full mr-2"
                      style={{ backgroundColor: status.color }}
                    />
                    <Text strong>{status.title}</Text>
                  </div>
                  <Badge
                    count={ticketsByStatus[status.key]?.length || 0}
                    style={{ backgroundColor: status.color }}
                  />
                </div>
              }
              size="small"
              className="h-full"
              style={{ minHeight: '600px' }}
            >
              <div className="space-y-2">
                {ticketsByStatus[status.key]?.map(ticket => (
                  <TicketCard key={ticket.id} ticket={ticket} />
                ))}
                {(!ticketsByStatus[status.key] || ticketsByStatus[status.key].length === 0) && (
                  <div className="text-center text-gray-400 py-8">
                    <Text type="secondary">ÊöÇÊó†Â∑•Âçï</Text>
                  </div>
                )}
              </div>
            </Card>
          </Col>
        ))}
      </Row>

      {/* Â∑•ÂçïËØ¶ÊÉÖÊ®°ÊÄÅÊ°Ü */}
      <Modal
        title="Â∑•ÂçïËØ¶ÊÉÖ"
        open={viewModalVisible}
        onCancel={() => setViewModalVisible(false)}
        footer={[
          <Button key="close" onClick={() => setViewModalVisible(false)}>
            ÂÖ≥Èó≠
          </Button>,
          <Button
            key="view"
            type="primary"
            onClick={() => {
              if (selectedTicket) {
                router.push(`/tickets/${selectedTicket.id}`);
                setViewModalVisible(false);
              }
            }}
          >
            Êü•ÁúãËØ¶ÊÉÖ
          </Button>,
        ]}
        width={800}
      >
        {selectedTicket && (
          <div className="space-y-4">
            <div>
              <Text strong>{selectedTicket.ticket_number}</Text>
              <Title level={4}>{selectedTicket.title}</Title>
            </div>
            <div>
              <Text>ÊèèËø∞:</Text>
              <Paragraph>{selectedTicket.description}</Paragraph>
            </div>
            <div className="grid grid-cols-2 gap-4">
              <div>
                <Text>Áä∂ÊÄÅ:</Text>
                <div>{selectedTicket.status}</div>
              </div>
              <div>
                <Text>‰ºòÂÖàÁ∫ß:</Text>
                <div>{selectedTicket.priority}</div>
              </div>
              <div>
                <Text>ÂàõÂª∫Êó∂Èó¥:</Text>
                <div>{dayjs(selectedTicket.created_at).format('YYYY-MM-DD HH:mm')}</div>
              </div>
              <div>
                <Text>Êõ¥Êñ∞Êó∂Èó¥:</Text>
                <div>{dayjs(selectedTicket.updated_at).format('YYYY-MM-DD HH:mm')}</div>
              </div>
            </div>
          </div>
        )}
      </Modal>
    </div>
  );
};

export default TicketKanban;
